.production {
  overflow: hidden;
  &__items {
    margin-top: 30px;
    @include screen(desktop) {
      margin-top: 152px;
    }
  }
  &__info {
    display: grid;
    gap: 24px;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    margin-top: 25px;
    @include screen(laptop) {
      grid-template-columns: repeat(2, minmax(0, 1fr));
      margin-top: 40px;
      gap: 30px;
    }
    p {
      font-family: $stapel;
      font-size: 18px;
      line-height: 21px;
      color: $black-primary;
      @include screen(laptop) {
        font-size: 24px;
        line-height: 28px;
      }
      @include screen(desktop) {
        font-size: 34px;
        line-height: 40px;
      }
    }
    span {
      font-size: 14px;
      line-height: 19px;
      color: $black-primary;
      @include screen(laptop) {
        font-size: 16px;
        line-height: 20px;
      }
      @include screen(desktop) {
        font-size: 20px;
        line-height: 25px;
      }
    }
  }
  &__item {
    position: relative;
    display: grid;
    align-items: center;
    transition: opacity $transition-default, background-color $transition-default;
    padding: 27px 0;
    border-top: 1px solid $black-border;
    border-bottom: 1px solid $black-border;
    margin-top: -1px;
    gap: 20px;
    @include screen(laptop) {
      opacity: 0.25;
      padding: 10px 0;
      gap: 40px;
      grid-template-columns: repeat(12, minmax(0, 1fr));
      &:before {
        content: '';
        position: absolute;
        left: -9999px;
        background-color: $blue-light;
        height: calc(100% + 2px);
        top: -1px;
        width: 9999px;
        border-top: 1px solid $black-border;
        border-bottom: 1px solid $black-border;
        transition: opacity $transition-default;
        opacity: 0;
      }
      &:after {
        content: '';
        position: absolute;
        background-color: $blue-light;
        right: -9999px;
        height: calc(100% + 2px);
        top: -1px;
        border-top: 1px solid $black-border;
        border-bottom: 1px solid $black-border;
        transition: opacity $transition-default;
        width: 9999px;
        opacity: 0;
      }
      &:hover {
        opacity: 1;
        background-color: $blue-light;
        .production__button {
          opacity: 1;
        }
        .production__image {
          opacity: 1;
        }
        &:before,
        &:after {
          opacity: 1;
        }
      }
    }
  }
  &__title {
    text-transform: uppercase;
    grid-column: span 5 / span 5;
    color: $black-dark;
    @include headline-4;
    @include screen(laptop) {
      font-size: 32px;
      font-family: $stapel;
      line-height: 38px;
    }
    @include screen(desktop) {
      @include headline-4;
    }
  }
  &__image {
    position: relative;
    z-index: 3;
    grid-column: span 2 / span 2;
    opacity: 0;
    transition: opacity $transition-default;
    display: none;
    height: 200px;
    img {
      height: 100%;
    }
    @include screen(laptop) {
      display: flex;
      align-items: center;
    }
  }
  &__content {
    display: grid;
    grid-column: span 5 / span 5;
    align-items: center;
    gap: 40px;
    @include screen(laptop) {
      grid-template-columns: repeat(5, minmax(0, 1fr));
    }
    p {
      font-size: 14px;
      line-height: 17.5px;
      color: $black-primary;
      grid-column: span 4 / span 4;
      @include screen(laptop) {
        font-size: 18px;
        line-height: 22px;
      }
      @include screen(desktop) {
        font-size: 24px;
        line-height: 30px;
      }
    }
  }
  &__button {
    position: absolute;
    width: 38px;
    height: 38px;
    top: 20px;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    transition: opacity $transition-default;
    border: 1px solid rgba($blue-dark, 0.6);
    svg {
      width: 14px;
    }
    @include screen(laptop) {
      position: relative;
      opacity: 0;
      width: 62px;
      height: 62px;
      top: auto;
      svg {
        width: auto;
      }
    }
  }
  &__elements {
    margin-top: 45px;
    @include screen(laptop) {
      margin-top: 80px;
    }
  }
  &__element {
    display: block;
    background-color: $blue-light;
    padding: 56px 0;
    position: relative;
    border-bottom: 1px solid #BDC8D0;
    @include screen(laptop) {
      padding: 110px 0;
    }
    &:first-child {
      border-top: 1px solid #BDC8D0;
    }
    &:before {
      content: '';
      position: absolute;
      left: -9999px;
      background-color: $blue-light;
      height: calc(100% + 2px);
      top: -1px;
      width: 9999px;
      border-top: 1px solid $black-border;
      border-bottom: 1px solid $black-border;
    }
    &:after {
      content: '';
      position: absolute;
      background-color: $blue-light;
      right: -9999px;
      height: calc(100% + 2px);
      top: -1px;
      border-top: 1px solid $black-border;
      border-bottom: 1px solid $black-border;
      width: 9999px;
    }
    &:hover {
      .production__bg {
        opacity: 1;
      }
      .production__head {
        h3 {
          color: $white-bright;
        }
        svg {
          path {
            fill: $white-bright;
          }
          circle {
            stroke: $white-bright;
          }
        }
      }
      .production__body {
        height: 100%;
        opacity: 1;
        p {
          height: 100%;
          margin-top: 40px;
          @include screen(laptop) {
            margin-top: 90px;
          }
          &:before {
            width: 100%;
          }
        }
      }
    }
  }
  &__container {
    position: relative;
    z-index: 2;
  }
  &__bg {
    position: absolute;
    inset: 0;
    transition: opacity $transition-default;
    opacity: 0;
    width: 100vw;
    img {
      height: 100%;
      object-fit: cover;
      width: 100%;
    }
  }
  &__head {
    display: flex;
    align-items: center;
    justify-content: space-between;
    h3 {
      font-family: $stapel;
      font-size: 20px;
      transition: color $transition-default;
      line-height: 26px;
      padding-right: 20px;
      text-transform: uppercase;
      @include screen(laptop) {
        max-width: 50%;
        font-size: 32px;
        line-height: 41px;
      }
      @include screen(desktop) {
        font-size: 45px;
        line-height: 58px;
      }
    }
    svg {
      min-width: 38px;
      min-height: 38px;
      @include screen(laptop) {
        width: auto;
        height: auto;
      }
      path {
        transition: fill $transition-default;
      }
      circle {
        transition: stroke $transition-default;
      }
    }
  }
  &__body {
    opacity: 0;
    height: 0;
    @include screen(laptop) {
      max-width: 50%;
    }
    p {
      font-size: 20px;
      padding-top: 20px;
      line-height: 25px;
      position: relative;
      color: $white-bright;
      transition: margin $transition-default;
      font-size: 14px;
      line-height: 19px;
      @include screen(laptop) {
        font-size: 16px;
        line-height: 20px;
      }
      @include screen(desktop) {
        font-size: 20px;
        line-height: 25px;
      }
      &:before {
        content: '';
        height: 1px;
        position: absolute;
        width: 0;
        left: 0;
        top: 0;
        background-color: #AFBFCB;
        transition: width $transition-slow;
      }
    }
  }
}