.breadcrumbs {
  padding: 30px 0 20px;
  margin-bottom: 30px;
  display: flex;
  align-items: center;
  margin-top: 10px;
  gap: 16px;
  overflow-x: auto;
  a,
  span {
    display: inline-block;
    font-size: 14px;
    line-height: 17px;
    white-space: nowrap;
  }
  span {
    color: #8695A4;
  }
  a {
    display: flex;
    align-items: center;
    color: #43484C;
  }
}