.certificates {
  overflow: hidden;
  background-color: #E4F4FF;
  .img-full {
    margin-top: 35px;
    @include screen(laptop) {
      margin-top: 60px;
    }
  }
  .content {
    h1 {
      text-transform: uppercase;
      color: $black-dark;
      font-family: $stapel;
      font-size: 32px;
      line-height: 38px;
      display: flex;
      align-items: center;
      gap: 15px;
      @include screen(laptop) {
        gap: 45px;
      }
      img {
        max-width: 80px;
        margin: 0;
        @include screen(laptop) {
          max-width: 223px;
        }
      }
      @include screen(laptop) {
        font-size: 94px;
        line-height: 112px;
      }
      @include screen(desktop) {
        @include headline-1;
      }
    }
    .headline {
      flex-direction: row;
      align-items: center;
    }
    .swiper-includes {
      .swiper {
        overflow: visible;
        margin-top: 16px;
        @include screen(laptop) {
          overflow: hidden;
        }
      }
    }
    .includes {
      &__slide {
        min-height: 282px;
        height: 282px;
        position: relative;
        height: 100%;
        img {
          height: 100%;
          width: 100%;
          object-fit: cover;
        }
        @include screen(laptop) {
          min-height: 257px;
          height: 257px;
          &:hover {
            .includes__content {
              opacity: 0;
            }
          }
        }
        @include screen(desktop) {
          min-height: 355px;
          height: 355px;
        }
      }
      &__content {
        position: absolute;
        background-color: #D5EBFC;
        display: flex;
        align-items: center;
        transition: opacity $transition-default;
        justify-content: center;
        z-index: 3;
        gap: 15px;
        flex-direction: column;
        width: 100%;
        height: 100%;
        @include screen(laptop) {
          gap: 20px;
        }
        p {
          margin: 0;
          text-align: center;
          color: $blue-dark;
          font-size: 14px;
          line-height: 17px;
          @include screen(laptop) {
            font-size: 16px;
            line-height: 20px;
          }
          @include screen(desktop) {
            font-size: 20px;
            line-height: 23px;
          }
        }
        span {
          color: #879AAA;
          font-size: 14px;
          line-height: 17px;
          @include screen(laptop) {
            font-size: 16px;
            line-height: 20px;
          }
          @include screen(desktop) {
            font-size: 20px;
            line-height: 25px;
          }
        }
      }
      &__image {
        position: relative;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
        &:before {
          height: 100%;
          content: '';
          inset: 0;
          position: absolute;
          background-color: rgba($blue-dark, 0.9);
        }
        img {
          margin: 0;
        }
      }
      &__more {
        position: absolute;
      }
    }
  }
}