.brand {
  &__block {
    background-color: #D5EBFC;
    display: grid;
    position: relative;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    @include screen(laptop) {
      padding: 60px 90px;
    }
    @include screen(desktop) {
      padding: 70px 120px;
    }
  }
  &__inner {
    overflow: hidden;
    padding-top: 75px;
  }
  &__content {
    display: flex;
    align-items: flex-start;
    gap: 11px;
    flex-direction: column;
    @include screen(laptop) {
      gap: 20px;
    }
  }
  &__subtitle {
    font-family: $stapel;
    @include screen(laptop) {
      font-size: 24px;
      line-height: 28px;
    }
    @include screen(desktop) {
      font-size: 34px;
      line-height: 41px;
    }
  }
  &__text {
    font-size: 14px;
    line-height: 19px;
    color: $black-primary;
    @include screen(laptop) {
      font-size: 16px;
      line-height: 20px;
    }
    @include screen(desktop) {
      font-size: 20px;
      line-height: 25px;
    }
  }
  &__image {
    position: absolute;
    right: 0;
    bottom: 0;
    img {
      max-width: 337px;
      @include screen(laptop) {
        max-width: 629px;
      }
      @include screen(desktop) {
        max-width: 100%;
      }
    }
  }
}