.products {
  overflow: hidden;
  background-color: $blue-primary;
  &__items {
    display: grid;
    gap: 4px;
    margin-top: 25px;
    @include screen(laptop) {
      margin-top: 40px;
      gap: 10px;
      grid-template-columns: repeat(3, minmax(0, 1fr));
    }
  }
  &__item {
    min-height: 267px;
    background-color: $blue-dark;
    position: relative;
    padding: 40px;
    overflow: hidden;
    display: block;
    width: 100%;
    height: 100%;
    &:before {
      content: '';
      position: absolute;
      left: 0;
      top: 80px;
      z-index: 1;
      transition: opacity $transition-slow;
      width: 385px;
      height: 385px;
      border-radius: 50%;
      background: rgba(#82C3FD, 0.2);
      filter: blur(40px);
      opacity: 0;
    }
    @include screen(laptop) {
      &:hover {
        &:before {
          opacity: 1;
        }
      }
    }
    @include screen(laptop) {
      padding: 37px 27px;
      min-height: 280px;
    }
    @include screen(desktop) {
      min-height: 387px;
      padding: 54px;
    }
    &.active {
      .directions__image {
        transform: scale(1);
      }
    }
  }
  &__image {
    z-index: 2;
    position: absolute;
    bottom: 0;
    right: 0;
    img {
      max-width: 100px;
      width: 100%;
      @include screen(laptop) {
        max-width: 230px;
        max-height: 190px;
      }
      @include screen(desktop) {
        max-width: 100%;
        max-height: 100%;
      }
    }
  }
  &__title {
    position: relative;
    z-index: 2;
    font-family: $stapel;
    text-transform: uppercase;
    color: $white-bright;
    font-size: 20px;
    line-height: 24px;
    @include screen(desktop) {
      font-size: 28px;
      line-height: 33px;
    }
  }
  .swiper-products {
    .swiper {
      overflow: visible;
      margin-top: 30px;
      @include screen(laptop) {
        overflow: hidden;
        margin-top: 40px;
      }
    }
  }
  .headline {
    flex-direction: row;
    .swiper-button-prev,
    .swiper-button-next {
      border-color: #93B5D2;
    }
  }
}