.catalog {
  &__items {
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    margin-top: 45px;
    @include screen(laptop) {
      margin-top: 60px;
      grid-template-columns: repeat(4, minmax(0, 1fr));
    }
  }
  &__item {
    display: flex;
    align-items: center;
    justify-content: center;
    aspect-ratio: 1 / 1;
    border: 1px solid $black-border;
    margin-left: -1px;
    transition: z-index $transition-default, border-color $transition-default;
    margin-top: -1px;
    img {
      width: auto;
      height: auto;
      aspect-ratio: 0;
      max-width: 120px;
      max-height: 80px;
      @include screen(laptop) {
        max-width: 180px;
        max-height: 100px;
      }
      @include screen(desktop) {
        max-width: 100%;
        max-height: 100%;
      }
    }
    @include screen(laptop) {
      &:hover {
        border-color: $black-primary;
        z-index: 2;
      }
    }
  }
}