* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-tap-highlight-color: transparent;
}

textarea,
input,
button,
select,
a {
  outline: none;
  border-radius: 0;

  font-weight: 400;
  color: $black-dark;
  font-family: $mulish;
  touch-action: manipulation;
}

html,
body {
  color: $black-dark;
  font-family: $mulish;
}

body {
  padding-bottom: var(--footer-height);
}

section {
  padding: 45px 0;
  @include screen(laptop) {
    padding: 80px 0;
  }
  @include screen(desktop) {
    padding: 100px 0;
  }
}

main {
  background-color: $white;
  z-index: 2;
  position: relative;
}

.headline {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  position: relative;
  gap: 24px;
  align-items: flex-start;
  @include screen(laptop) {
    flex-direction: row;
    gap: 40px;
  }
  p {
    width: 100%;
    color: $black-primary;
    max-width: 476px;
    @include text-1;
    @include screen(laptop) {
      font-family: $stapel;
      font-size: 24px;
      line-height: 30px;
    }
    @include screen(desktop) {
      max-width: 664px;
      @include text-1;
    }
  }
  .swiper-navigation {
    display: flex;
    align-items: center;
    gap: 10px;
    .swiper-button-prev,
    .swiper-button-next {
      position: relative;
      position: static;
      margin: 0;
      width: 38px;
      height: 38px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      border: 1px solid rgba($blue-dark, 0.6);
      @include screen(laptop) {
        width: 62px;
        height: 62px;
      }
      svg {
        width: 14px;
        @include screen(laptop) {
          width: 23px;
        }
      }
      &:before,
      &:after {
        display: none;
      }
    }
  }
}

.button {
  display: flex;
  gap: 12px;
  border: 1px solid $white-primary;
  border-radius: 47px;
  transition: background-color $transition-default, border-color $transition-default;
  align-items: center;
  white-space: nowrap;
  padding: 20px 28px;
  @include screen(desktop) {
    padding: 28px 43px;
  }
  span {
    text-transform: uppercase;
    font-family: $stapel;
    font-size: 16px;
    line-height: 19.2px;
    transition: color $transition-default;
    font-weight: 400;
    @include screen(desktop) {
      line-height: 33.6px;
      font-size: 28px;
    }
  }
  &-blue {
    border: 1px solid $white;
    background-color: $blue-dark;
    span {
      color: $white;
    }
    @include screen(laptop) {
      &:hover {
        border-color: $blue-dark;
        background-color: $white;
        span {
          color: $black-dark;
        }
        .button__icon {
          svg {
            path {
              fill: $black-dark;
            }
          }
        }
      }
    }
  }
  &__icon {
    max-width: 21px;
    max-height: 21px;
    @include screen(desktop) {
      max-width: 33px;
      max-height: 33px;
    }
    svg {
      width: 100%;
      path {
        transition: fill $transition-default;
      }
    }
  }
}

.render {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99999;

  width: 100%;
  height: 100%;

  background-color: $white;
}

.wrapper {
  margin: 0 auto;
  padding: 0 #{$container-padding};
  width: 100%;
  max-width: calc(#{$mobile-wrapper} + #{$container-padding * 2});
  @include screen(tablet) {
    max-width: calc(#{$screen-tablet} + #{$container-padding * 2});
  }
  @include screen(laptop) {
    max-width: calc(#{$screen-laptop} + #{$container-padding * 2});
  }
  @include screen(desktop) {
    max-width: calc(#{$screen-desktop} + #{$container-padding * 2});
  }
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition-duration: 0.01ms !important;
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    scroll-behavior: auto !important;
  }
}

.lg-sub-html,
.lg-download,
.lg-counter,
.lg-outer .lg-pager,
.lg-pager-thumb-cont {
  display: none;
}

.lg-toolbar {
  height: 0;
}

.lg-backdrop.in {
  opacity: 0.9;
}
