.typography {
  overflow: hidden;
  .img-full {
    margin-top: 35px;
    @include screen(laptop) {
      margin-top: 60px;
    }
  }
  .content {
    h1 {
      text-transform: uppercase;
      color: $black-dark;
      font-family: $stapel;
      font-size: 32px;
      line-height: 38px;
      display: flex;
      align-items: center;
      gap: 15px;
      @include screen(laptop) {
        gap: 45px;
      }
      img {
        max-width: 80px;
        margin: 0;
        @include screen(laptop) {
          max-width: 223px;
        }
      }
      @include screen(laptop) {
        font-size: 94px;
        line-height: 112px;
      }
      @include screen(desktop) {
        @include headline-1;
      }
    }
    .headline {
      flex-direction: row;
      align-items: center;
      margin-top: 60px;
      @include screen(laptop) {
        margin-top: 80px;
      }
      @include screen(desktop) {
        margin-top: 100px;
      }
    }
    h2 {
      text-transform: uppercase;
      color: $black-dark;
      font-family: $stapel;
      font-size: 26px;
      line-height: 31px;
      @include screen(laptop) {
        font-size: 60px;
        line-height: 72px;
      }
      @include screen(desktop) {
        @include headline-2;
      }
    }
    h3 {
      text-transform: uppercase;
      color: $black-dark;
      font-family: $stapel;
      font-size: 22px;
      line-height: 26px;
      margin-top: 60px;
      @include screen(laptop) {
        font-size: 40px;
        line-height: 48px;
        margin-top: 80px;
      }
      @include screen(desktop) {
        margin-top: 100px;
        @include headline-3;
      }
    }
    p.stapel {
      font-family: $stapel;
      font-size: 18px;
      line-height: 25px;
      @include screen(laptop) {
        margin-top: 40px;
        font-size: 24px;
        line-height: 28px;
      }
      @include screen(desktop) {
        font-size: 34px;
        line-height: 40px;
      }
    }
    p.w-50 {
      @include screen(laptop) {
        max-width: 50%;
      }
    }
    p {
      color: $black-primary;
      font-size: 14px;
      line-height: 20px;
      margin-top: 20px;
      @include screen(laptop) {
        font-size: 16px;
        line-height: 20px;
      }
      @include screen(desktop) {
        font-size: 20px;
        margin-top: 40px;
        line-height: 25px;
      }
    }
    .cols {
      display: grid;
      grid-template-columns: repeat(1, minmax(0, 1fr));
      gap: 20px;
      margin-top: 25px;
      @include screen(laptop) {
        margin-top: 0;
        grid-template-columns: repeat(2, minmax(0, 1fr));
      }
      @include screen(desktop) {
        gap: 30px;
      }
      &-mob {
        margin-top: 0;
        grid-template-columns: repeat(2, minmax(0, 1fr));
        img {
          aspect-ratio: 1 / 1;
          object-fit: cover;
          @include screen(laptop) {
            aspect-ratio: inherit;
          }
        }
      }
    }
    .img-text {
      display: grid;
      gap: 35px;
      margin-top: 40px;
      @include screen(laptop) {
        gap: 30px;
        grid-template-columns: 66% 33%;
        margin-top: 60px;
      }
      &-reversed {
        @include screen(laptop) {
          grid-template-columns: 33% 66%;
        }
      }
      &__image {
        order: 2;
        @include screen(laptop) {
          order: inherit;
        }
        img {
          margin-top: 0;
        }
      }
      &__content {
        p {
          &:first-child {
            margin-top: 0;
          }
        }
      }
    }
    img {
      margin-top: 35px;
      user-select: none;
      @include screen(laptop) {
        margin-top: 60px;
      }
    }
    i {
      color: #939BA2;
      display: inline-block;
      font-style: normal;
      font-size: 12px;
      line-height: 15px;
      margin-top: 15px;
      @include screen(laptop) {
        margin-top: 25px;
        font-size: 16px;
        line-height: 20px;
      }
      @include screen(desktop) {
        font-size: 20px;
        line-height: 25px;
      }
    }
    blockquote {
      position: relative;
      padding: 60px 0;
      margin-top: 60px;
      background-color: $blue-light;
      @include screen(laptop) {
        padding: 80px 0;
        margin-top: 80px;
      }
      @include screen(desktop) {
        padding: 100px 0;
        margin-top: 100px;
      }
      &:before {
        content: '';
        position: absolute;
        left: -9999px;
        background-color: $blue-light;
        height: calc(100% + 2px);
        top: -1px;
        width: 9999px;
        border-top: 1px solid $black-border;
        border-bottom: 1px solid $black-border;
        transition: opacity $transition-default;
      }
      &:after {
        content: '';
        position: absolute;
        background-color: $blue-light;
        right: -9999px;
        height: calc(100% + 2px);
        top: -1px;
        border-top: 1px solid $black-border;
        border-bottom: 1px solid $black-border;
        transition: opacity $transition-default;
        width: 9999px;
      }
      p {
        padding-left: 40px;
        font-family: $stapel;
        margin: 0;
        border-left: 3px solid $blue-dark;
        font-size: 18px;
        line-height: 25px;
        @include screen(laptop) {
          font-size: 24px;
          line-height: 28px;
          border-left: 5px solid $blue-dark;
        }
        @include screen(desktop) {
          font-size: 34px;
          line-height: 40px;
        }
      }
    }
    h4 {
      text-transform: uppercase;
      color: $black-dark;
      font-family: $stapel;
      font-size: 20px;
      line-height: 24px;
      margin-top: 60px;
      @include screen(laptop) {
        margin-top: 80px;
        font-size: 32px;
        line-height: 38px;
      }
      @include screen(desktop) {
        margin-top: 100px;
        @include headline-4;
      }
    }
    .items {
      margin-top: 40px;
      .item {
        padding: 20px 0;
        display: grid;
        grid-template-columns: 80% 20%;
        border-bottom: 1px solid $black-border;
        @include screen(laptop) {
          padding: 30px 0;
          display: flex;
          justify-content: space-between;
        }
        p {
          margin: 0;
          text-transform: uppercase;
          font-size: 14px;
          line-height: 19px;
          color: $black-primary;
          @include screen(laptop) {
            line-height: 20px;
            font-size: 16px;
          }
          @include screen(desktop) {
            font-size: 20px;
            line-height: 25px;
          }
        }
        span {
          text-align: right;
          font-size: 14px;
          line-height: 19px;
          color: $black-primary;
          @include screen(laptop) {
            line-height: 20px;
            font-size: 16px;
          }
          @include screen(desktop) {
            font-size: 20px;
            line-height: 25px;
          }
        }
        &:first-child {
          border-top: 1px solid $black-border;
        }
      }
    }
    table {
      width: 100%;
      border: 1px solid $black-border;
      margin-top: 40px;
      @include screen(laptop) {
        margin-top: 60px;
      }
      tr {
        &:nth-child(2n+1) {
          td {
            background-color: #F2F9FE;
          }
        }
        td {
          text-align: center;
          border: 1px solid $black-border;
          text-transform: uppercase;
          padding: 21px 0;
          font-size: 14px;
          line-height: 17px;
          color: $black-primary;
          @include screen(laptop) {
            padding: 39px 0;
            font-size: 16px;
            line-height: 20px;
          }
          @include screen(desktop) {
            font-size: 20px;
            padding: 53px 0;
            line-height: 25px;
          }
        }
      }
    }
    .swiper-gallery {
      .swiper {
        overflow: visible;
        &-slide {
          transition: opacity $transition-default;
          opacity: 0.5;
          &-active {
            opacity: 1;
          }
        }
      }
    }
    .swiper-includes {
      .swiper {
        overflow: visible;
        margin-top: 16px;
        @include screen(laptop) {
          overflow: hidden;
        }
      }
    }
    .includes {
      &__slide {
        min-height: 282px;
        height: 282px;
        position: relative;
        height: 100%;
        img {
          height: 100%;
          width: 100%;
          object-fit: cover;
        }
        @include screen(laptop) {
          min-height: 257px;
          height: 257px;
          &:hover {
            .includes__content {
              opacity: 0;
            }
          }
        }
        @include screen(desktop) {
          min-height: 355px;
          height: 355px;
        }
      }
      &__content {
        position: absolute;
        background-color: #D5EBFC;
        display: flex;
        align-items: center;
        transition: opacity $transition-default;
        justify-content: center;
        z-index: 3;
        gap: 15px;
        flex-direction: column;
        width: 100%;
        height: 100%;
        @include screen(laptop) {
          gap: 20px;
        }
        p {
          margin: 0;
          text-align: center;
          color: $blue-dark;
          font-size: 14px;
          line-height: 17px;
          @include screen(laptop) {
            font-size: 16px;
            line-height: 20px;
          }
          @include screen(desktop) {
            font-size: 20px;
            line-height: 23px;
          }
        }
        span {
          color: #879AAA;
          font-size: 14px;
          line-height: 17px;
          @include screen(laptop) {
            font-size: 16px;
            line-height: 20px;
          }
          @include screen(desktop) {
            font-size: 20px;
            line-height: 25px;
          }
        }
      }
      &__image {
        position: relative;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
        &:before {
          height: 100%;
          content: '';
          inset: 0;
          position: absolute;
          background-color: rgba($blue-dark, 0.9);
        }
        img {
          margin: 0;
        }
      }
      &__more {
        position: absolute;
      }
    }
  }
}