.not-found {
  position: relative;
  padding: 153px 0 98px;
  overflow: hidden;
  @include screen(laptop) {
    padding: 216px 0 177px;
  }
  @include screen(desktop) {
    padding: 254px 0 209px;
  }
  &__wrapper {
    display: flex;
    align-items: center;
  }
  &__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    z-index: 2;
    position: relative;
  }
  &__title {
    font-family: $stapel;
    text-transform: uppercase;
    font-size: 26px;
    line-height: 31px;
    @include screen(laptop) {
      font-size: 40px;
      line-height: 48px;
    }
    @include screen(desktop) {
      font-size: 60px;
      line-height: 72px;
    }
  }
  &__text {
    margin-top: 20px;
    font-size: 14px;
    line-height: 17px;
    color: $black-primary;
    @include screen(laptop) {
      font-size: 16px;
      line-height: 20px;
    }
    @include screen(desktop) {
      font-size: 20px;
      line-height: 25px;
    }
  }
  &__button {
    margin-top: 35px;
    @include screen(laptop) {
      margin-top: 45px;
    }
    @include screen(desktop) {
      margin-top: 50px;
    }
  }
  &__image {
    position: absolute;
    right: 0;
    top: 0;
    max-width: 800px;
    @include screen(laptop) {
      top: 0;
      max-width: 100%;
    }
  }
}